import * as React from "react";
import tinyImg from "../../images/Tiny_Home.png";
import zanelliImg from "../../images/Zanelli_Home.png";
import bakersLaneImg from "../../images/BakersLane_Home.jpg";

const pageStyles = {
  height: "80vh",
};

const products = [
  {
    id: "tiny",
    title: "TINY - Ultraviolet Sunscreen Guard",
    tags: ["Sunscreen", "Lotion"],
    imgSrc: tinyImg,
  },
  {
    id: "zanelli",
    title: "Zenelli - The Pizza Room",
    tags: ["Italian Restaurant"],
    imgSrc: zanelliImg,
  },
  {
    id: "bakersLane",
    title: "Bakers Lane - Mostly Baked",
    tags: ["Breads", "Bagels"],
    imgSrc: bakersLaneImg,
  },
];

export default function HeroProductOverview({ children }) {
  return (
    <>
      {products.map((elem, idx) => {
        return (
          <div className="py-2 px-4" key={elem.id}>
            <div className="row fw-bold">
              <div className="col">
                <p className="">{elem.title}</p>
              </div>
              <div className="col text-end">
                {elem.tags.map((tag, idx) => {
                  return (
                    <span key="idx">
                      <button
                        type="button"
                        className="btn btn-outline-dark btn-sm rounded-pill"
                      >
                        {tag}
                      </button>
                      &nbsp;&nbsp;
                    </span>
                  );
                })}
              </div>
            </div>
            <div className="row">
              <div className="col">
                <img
                  alt="Lane7"
                  src={elem.imgSrc}
                  width={"100%"}
                  className="d-inline-block align-text-top"
                />
              </div>
            </div>
          </div>
        );
      })}
    </>
  );
}
