import * as React from "react";
import { Link } from "gatsby";
import logoImg from "../images/icon.png";

const Footer = ({ pageTitle, children }) => {
  return (
    <div class="container-fluid">
      <div class="row p-2 align-items-center">
        <div class="col-xs-12 col-md-4 align-self-center">
          <div className="nav text-bg-light">
            <p className="nav-link text-bg-light">FOLLOW US</p>
            <Link to="/" className="nav-link text-bg-light" aria-current="page">
              Instagram
            </Link>
            <Link to="/" className="nav-link text-bg-light" aria-current="page">
              LinkedIn
            </Link>
          </div>
        </div>
        <div class="col-xs-12 col-md-4 text-center">
          <p className="fw-bold">&copy; LANE7</p>
        </div>
        <div className="col-xs-12 col-md-4 text-sm-center text-md-end">
          <p className="fs-1 fw-bold">LANE7</p>
        </div>
      </div>
    </div>
  );
};

export default Footer;
